import { useLazyQuery } from '@apollo/client';
import { useFormik } from 'formik';
import PropTypes from 'prop-types';
import { SEARCH_PRODUCT } from 'query/Prodact/product';
import { useEffect } from 'react';
import { useState } from 'react';
import * as Yup from 'yup';
import AddProductToCartModalUI from './AddProductToCartModalUI';

const AddProductToCartModal = ({ open, onClose, setFormData }) => {
  const [selectedProduct, setSelectedProduct] = useState({});
  const [options, setOptions] = useState([]);
  const [loadingOptions, setLoadingOptions] = useState(false);
  const [searchBar, setSearchBar] = useState('');
  const [getOptionsProduct] = useLazyQuery(SEARCH_PRODUCT, { fetchPolicy: 'no-cache' });
  const formik = useFormik({
    initialValues: {
      product: {},
      color: "",
      size: "",
      qty: 0,

    },
    validationSchema: Yup.object().shape({
      color: Yup.string(),
      size: Yup.string(),
      qty: Yup.string().required("this feild is required")
    }),
    onSubmit: (values) => {
      let newObject = refactor(values);
      setFormData(oldFormData => {
        return [...oldFormData, newObject];
      });
    },
  });
  const refactor = (value) => {
    console.log(value.product);
    let item = value.product;
    let price = value.product?.variants[0]?.size[0]?.price ? value.product?.variants[0]?.size[0]?.price : value.product?.variants[0]?.price ? value.product?.variants[0]?.price : value.product?.price;
    return {
      id: item.id,
      name: item.name + (value.color || value.color == "" ? " - " + value.color : "") + (value.size || value.size == "" ? "," + value.size : ""),
      price: price,
      qty: value.qty,
      sale_price: item.sale_price,
      short_desc: item.short_desc,
      slug: item.slug,
      cost: value.product.cost,
      sum: value.qty * price,
      saletype: "clintSalesCount",
      until: item.until ? item.until : null,
      sm_pictures: item.sm_pictures.map((item) => {
        const pic = {
          width: item.width,
          height: item.height,
          url: item.url,
        };
        return pic;
      }),
      variants: [],
      stock: item.stock,
      sold: item.sold,
    };
  };
  useEffect(() => {
    setLoadingOptions(true);
    setOptions([]);
    const getData = setTimeout(() => {
      getOptionsProduct({
        variables: {
          searchProductInput: {
            "page": 1,
            "size": 5,
            "keyword": searchBar
          }
        }
      }).then(rez => {
        setLoadingOptions(false);
        setOptions(rez.data.searchProduct.map(i => { return { ...i, label: i.name + " - " + i.id, key: i.id }; }));
      });
    }, 900);

    return () => clearTimeout(getData);
  }, [searchBar]);

  useEffect(() => {
    formik.setValues({
      product: selectedProduct,
      color: "",
      size: "",
      qty: 0
    });
  }, [selectedProduct]);
  return (
    <AddProductToCartModalUI
      open={open}
      onClose={onClose}
      formik={formik}
      setSearchBar={setSearchBar}
      optionLoading={loadingOptions}
      options={options}
      selectedProduct={selectedProduct}
      setSelectedProduct={setSelectedProduct}
    />
  );
};
export default AddProductToCartModal;

AddProductToCartModal.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  setFormData: PropTypes.func.isRequired
};
