import React, {useEffect, useState} from 'react';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import CancelIcon from '@mui/icons-material/Cancel';
import {Fonts} from 'shared/constants/AppEnums';
import {styled} from '@mui/material/styles';

const StyledTableCell = styled(TableCell)(() => ({
  fontSize: 14,
  padding: 8,
  '&:first-of-type': {
    paddingLeft: 20,
  },
  '&:last-of-type': {
    paddingRight: 20,
  },
}));

const TableItem = ({data, onRemoveClicked}) => {
  const [quantity, setQuantity] = useState(0);
  useEffect(() => {
    setQuantity(data.qty);
  }, [data]);
  return (
    <TableRow key={data.id} className='item-hover'>
      <StyledTableCell>
        <Box display='flex'>
          <Avatar sx={{mr: 3.5}} src={data.sm_pictures[0].url} />
          <Box>
            <Box fontSize={14} fontWeight={Fonts.MEDIUM}>
              {data.slug}
            </Box>
            <Box color='text.secondary' fontSize={14}>
              Brand: {data.name}
            </Box>
          </Box>
        </Box>
      </StyledTableCell>
      <StyledTableCell align='left' fontWeight={Fonts.MEDIUM}>
        ${data.sale_price ? data.sale_price.toFixed(2) : data.price.toFixed(2)}
      </StyledTableCell>
      <StyledTableCell align='left'>
        <Box
          border={1}
          borderRadius={4}
          display='flex'
          borderColor='text.secondary'
          alignItems='center'
          justifyContent='center'
          width={100}
          height={36}
        >
          <Box component='span' px={3}>
            {quantity ? quantity : 0}
          </Box>
        </Box>
      </StyledTableCell>
      <StyledTableCell align='left' fontWeight={Fonts.MEDIUM}>
        $
        {(data.sale_price
          ? data.sale_price.toFixed(2)
          : data.price.toFixed(2)) * quantity}
      </StyledTableCell>
      <StyledTableCell component='th' scope='row'>
        <CancelIcon onClick={() => onRemoveClicked(data)} />
      </StyledTableCell>
    </TableRow>
  );
};

export default TableItem;

TableItem.propTypes = {
  data: PropTypes.object.isRequired,
  items: PropTypes.array,
  onRemoveClicked: PropTypes.func,
};
