import {
  Autocomplete,
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Fade,
  Grid,
  InputLabel,
  Modal,
  TextField,
} from '@mui/material';
import PropTypes from 'prop-types';

const AddProductToCartModalUI = ({
  formik,
  onClose,
  open,
  setSearchBar,
  optionLoading,
  options,
  selectedProduct,
  setSelectedProduct,
}) => {
  // const top100Films = [
  //   { label: 'The Shawshank Redemption', year: 1994 }
  // ];
  return (
    <Modal open={open} onClose={() => onClose(false)}>
      <Fade in={open}>
        <form onSubmit={formik.handleSubmit}>
          <Box width={{ sx: '100%', lg: '60%' }}
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
            }}
          >
            <Card>
              <CardHeader title="Add Order" />
              <CardContent>

                <Grid container spacing={3}>
                  <Grid item xs={12} lg={6}>

                    <InputLabel >product name</InputLabel>
                    <Autocomplete

                      disablePortal
                      loading={optionLoading}
                      id="productsOptions"
                      options={options}
                      sx={{ width: 300 }}
                      onChange={(e, value) => { setSelectedProduct(value); }}
                      renderInput={(params) => <TextField required onChange={(e) => setSearchBar(e.target.value)} {...params} />}
                    />

                  </Grid>

                  <Grid item xs={12} lg={6}>
                    <InputLabel >qty</InputLabel>
                    <TextField
                      required
                      name="qty"
                      value={formik.values.qty}
                      onChange={formik.handleChange} />
                  </Grid>
                  {selectedProduct?.variants && selectedProduct?.variants[0]?.color && selectedProduct?.variants[0]?.color != 'default' &&
                    <>
                      <Grid item xs={12} lg={6}>
                        <InputLabel >colors</InputLabel>
                        <Autocomplete
                          disablePortal
                          id="color"
                          name="color"
                          value={{ label: formik.values.color }}
                          options={selectedProduct.variants.map(i => { return { label: i.color_name, key: i.color }; })}
                          sx={{ width: 300 }}
                          onChange={(_, value) => formik.setFieldValue("color", value?.label ? value.label : "")}
                          renderInput={(params) => <TextField required {...params} />}
                        />
                      </Grid>

                    </>
                  }
                  {selectedProduct?.variants && selectedProduct.variants[0]?.size && selectedProduct.variants[0].size[0]?.id &&
                    <>
                      <Grid item xs={12} lg={6}>
                        <InputLabel >size</InputLabel>
                        <Autocomplete
                          disablePortal
                          id="size"
                          name="size"
                          value={{ label: formik.values.size }}
                          options={selectedProduct.variants[0]?.size.map(i => { return { label: i.name, key: i.name }; })}
                          sx={{ width: 300 }}
                          onChange={(e, value) => formik.setFieldValue("size", value?.label ? value.label : "")}
                          renderInput={(params) => <TextField required {...params} />}
                        />
                      </Grid>
                    </>
                  }
                </Grid>
                <Box m={5} textAlign={'center'}>
                  <Button type='submit' variant='contained' >Add Product</Button>
                </Box>
              </CardContent>
            </Card>
          </Box >
        </form>
      </Fade>
    </Modal >
  );
};

export default AddProductToCartModalUI;
AddProductToCartModalUI.propTypes = {
  open: PropTypes.bool.isRequired,
  formik: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
  setSearchBar: PropTypes.func.isRequired,
  optionLoading: PropTypes.bool.isRequired,
  options: PropTypes.array.isRequired,
  selectedProduct: PropTypes.object.isRequired,
  setSelectedProduct: PropTypes.func.isRequired
};
